import React from 'react'

class Success extends React.Component {

	constructor(props) {
		super(props);
		this.handleSubmit = this.handleSubmit.bind(this);

		this.state = {
	      display:"inherit"
	    };

	}

	componentDidMount() {
	}

	handleSubmit(e){
	    this.setState({display:"none"})
	}

	render() {
		return (

			<div className="error" style={{display:this.state.display}}>
      			<div className="errorMsg" >
      			<span className="errorTitle">Success</span><br /><br />
      			수강신청이 완료 되었습니다.<br />
      			2021년 1월부터 시작될 대디 마스터 클래스의 소식을 기다려주세요.<br />
      			<button className="inputButton" onClick={this.handleSubmit}>ok</button>
      			</div>
      			</div>
		);
	}

    
}

export default Success