import React from 'react';
import { ScreenWidth } from './ScreenWidth';


class Error extends React.Component {

	constructor(props) {
		super(props);
		this.handleSubmit = this.handleSubmit.bind(this);
		var defaultErrorMsg = "none"
		var noatErrorMsg = "none"
		var alreadyErrorMsg = "none"
		var domainErrorMsg = "none"
		var emptyErrorMsg = "none"
		console.log(this.props.name);
		if (this.props.name == "default"){

			defaultErrorMsg = "inherit"
		}else if(this.props.name == "already"){
			alreadyErrorMsg = "inherit"
		}else if(this.props.name == "noat"){
			alreadyErrorMsg = "inherit"
		}else if(this.props.name == "domain"){
			domainErrorMsg = "inherit"
		}else if(this.props.name == "empty"){
			emptyErrorMsg = "inherit"
		}else{
			defaultErrorMsg = "inherit"
		}
		this.state = {
	      display:"inherit",
	      displayForm:"none",
	      defaultErrorMsg: defaultErrorMsg,
	      noatErrorMsg: noatErrorMsg,
	      alreadyErrorMsg: alreadyErrorMsg,
	      domainErrorMsg: domainErrorMsg,
	      emptyErrorMsg:emptyErrorMsg
	    };
	}

	componentDidMount() {
	}

	handleSubmit(e){
	    this.setState({display:"none",
	      displayForm:"inherit"})
	}

	render() {
		return (
			<div>
			
			<ScreenWidth>
        		{(width) => <div className="forForm" style={{display:this.state.displayForm}}>{width}</div>}
      		</ScreenWidth>

      		<div className="error" style={{display:this.state.display}}>
      			<div className="errorMsg" style={{display:this.state.defaultErrorMsg}}>
      			<span className="errorTitle">Error!</span><br /><br />
      			에러가 발생했습니다.<br />
      			다시 시도해주세요.<br />
      			<button className="inputButton" onClick={this.handleSubmit}>ok</button>
      			</div>
      			<div className="errorMsg" style={{display:this.state.noatErrorMsg}}>
      			<span className="errorTitle">Error!</span><br /><br />
      			유효한 이메일 주소가 아닙니다.<br />
      			다시 시도해주세요.<br />
      			<button className="inputButton" onClick={this.handleSubmit}>ok</button>
      			</div>
      			<div className="errorMsg" style={{display:this.state.alreadyErrorMsg}}>
      			<span className="errorTitle">Error!</span><br /><br />
      			이미 등록된 주소입니다.<br />
      			<button className="inputButton" onClick={this.handleSubmit}>ok</button>
      			</div>
      			<div className="errorMsg" style={{display:this.state.domainErrorMsg}}>
      			<span className="errorTitle">Error!</span><br /><br />
      			유효한 이메일 주소가 아닙니다.<br />
      			다시 시도해주세요.<br />
      			<button className="inputButton" onClick={this.handleSubmit}>ok</button>
      			</div>
      			<div className="errorMsg" style={{display:this.state.emptyErrorMsg}}>
      			<span className="errorTitle">Error!</span><br /><br />
      			이름과 이메일 주소를 모두 입력해주세요.<br />
      			<button className="inputButton" onClick={this.handleSubmit}>ok</button>
      			</div>
				

			</div>
      </div>
		);
	}

    
}

export default Error