import React from 'react';
import useMailchimp from './Hooks'
import Status from './Hooks'
import jsonp from 'jsonp'

import Loading from './Loading';
import Error from './Error';
import Success from './Success';

export function useScreenWidth() {
  // const SubscribeForm = () => {
  const [form, setForm] = React.useState({firstName: '', email: ''})
  const {subscribe, status, error, value} = useMailchimp('https://gmail.us7.list-manage.com/subscribe/post?u=7247676ecb0306e26c5320a78&amp;id=2a8a1ad9a4')

  const handleInputChange = event => {
    const target = event.target
    const name = target.name
    const value = target.value
    setForm(form => ({
      ...form,
      [name]: value,
    }))
  }

  const handleSubmit = () => {
    // We are performing some simple validation here.
    // I highly recommend checking a fully-fledged solution
    // for forms like https://react-hook-form.com/



    if (form.firstName === '' || form.email === '') {
    	// console.log("empty");
    	return}

    subscribe({
      FNAME: form.firstName,
      EMAIL: form.email,
    })
  }

  const afterSubmission = event => {
    event.preventDefault();
	}

if (status === "LOADING") {
    return <Loading />
  }

  if (status === "ERROR") {
  	if (error != undefined) {
  		if (error.includes('An email address must contain')) {
  		console.log("heheh");
  		return <Error name="noat" />
	  	// return <Loading />
	    // return <Success />

	  }else if (error.includes('The domain portion of the email address is invalid')) {
  		console.log("heheh2");
  		return <Error name="domain" />
	  	// return <Loading />
	    // return <Success />
	    
	  }else if (error.includes('already subscribed')) {
  	  	console.log("heheh3", value);
  	  	return <Error name="already" />
	  	// return <Loading />
	    // return <Success />
	  }else{
	  	console.log("wherer");
	  	return <Error name="default" />
	  }
  	}else{
  		console.log("wherer2");
  		// return <Error name="에러가 발생했습니다. 다시 시도해주세요." />
  	}
    
  }
  if (status === "SUCCESS") {
  	return <Success />
  }

  return (
  	<div>
    <form className="form" style={{display:"inherit"}} onSubmit = {afterSubmission} autocomplete="off" >
      <input
        id="firstName"
        name="firstName"
        placeholder="name"
        onChange={handleInputChange}
        className="input"
      />
      <br />
      <input className="input" id="email" name="email" placeholder="email" onChange={handleInputChange} />
      <br /> 
      <button className="inputButton" onClick={handleSubmit}>Subscribe</button>
    </form>
    </div>
  )
// }

}

